@keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}
@-webkit-keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}
@-moz-keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}
@-ms-keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}
@keyframes move-clouds-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 10000px 0;
  }
}
@-webkit-keyframes move-clouds-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 10000px 0;
  }
}
@-moz-keyframes move-clouds-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 10000px 0;
  }
}
@-ms-keyframes move-clouds-back {
  from {
    background-position: 0;
  }
  to {
    background-position: 10000px 0;
  }
}
@keyframes move-lambo-back {
  from {
    background-position: -100% 0;
  }
  to {
    background-position: 200% 0;
  }
}
@-webkit-keyframes move-lambo-back {
  from {
    background-position: -100% 0;
  }
  to {
    background-position: 200% 0;
  }
}
@-moz-keyframes move-lambo-back {
  from {
    background-position: -100% 0;
  }
  to {
    background-position: 200% 0;
  }
}
@-ms-keyframes move-lambo-back {
  from {
    background-position: -100% 0;
  }
  to {
    background-position: 200% 0;
  }
}
.stars,
.twinkling,
.clouds {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: block;
}

.lambo {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: auto;
  display: block;
  margin-top: 5%;
}

.stars {
  background: #000 url(images/stars.png) repeat top center;
  z-index: 0;
}
.twinkling {
  background: transparent url(images/twinkling.png) repeat top center;
  z-index: 1;
  -moz-animation: move-twink-back 200s linear infinite;
  -ms-animation: move-twink-back 200s linear infinite;
  -o-animation: move-twink-back 200s linear infinite;
  -webkit-animation: move-twink-back 200s linear infinite;
  animation: move-twink-back 200s linear infinite;
}
.clouds {
  background: transparent url(images/clouds.png) repeat top center;
  z-index: 3;
  -moz-animation: move-clouds-back 200s linear infinite;
  -ms-animation: move-clouds-back 200s linear infinite;
  -o-animation: move-clouds-back 200s linear infinite;
  -webkit-animation: move-clouds-back 200s linear infinite;
  animation: move-clouds-back 200s linear infinite;
}
.lambo {
  background: transparent url(images/lambomoon.png) no-repeat top center;
  z-index: 3;
  -moz-animation: move-lambo-back 10s linear infinite;
  -ms-animation: move-lambo-back 10s linear infinite;
  -o-animation: move-lambo-back 10s linear infinite;
  -webkit-animation: move-lambo-back 10s linear infinite;
  animation: move-lambo-back 10s linear infinite;
}
